import React from 'react'
import '../Styles/social_network.css'

const SocialNetwork = () => {

    const anim = () =>{
        let navlinks =document.querySelectorAll('.social-network .a')

        navlinks.forEach(link =>{
            link.addEventListener('mouseover', (e) => {
                let attrX = e.offset -20;
                let attrY = e.offset -13;

                link.style.transform = `translate(${attrX}px,${attrY}px)`;
            })
        })
    }

    return (
       <div className="social-network">
           <ul className="content">
                <li><a href="https://www.facebook.com" className="hover" onMouseOver={anim}><i className="fab fa-facebook-f"></i></a></li>
                <li>
                <a href="https://www.linkedin.com/in/ivan-de-villers-grand-champs-59257a111" className="hover"><i class="fab fa-linkedin"></i></a>
                </li>
           </ul>
       </div>
    )
}

export default SocialNetwork
