import React from 'react'
import { NavLink } from 'react-router-dom'
import '../Styles/navigation.css'

const Navbar = () => {
    return(
        <div className="navigation">
            <ul>
                <NavLink to="/" exact className="hover"
                activeClassName="nav-active">
                    <li>accueil</li>
                </NavLink>
                <NavLink to="P_Folios" className="hover"
                activeClassName="nav-active">
                    <li>P_Folios</li>
                </NavLink>
                <NavLink to="/formules" className="hover"
                activeClassName="nav-active">
                    <li>formules</li>
                </NavLink>
                <NavLink to="/contact" exact className="hover"
                activeClassName="nav-active">
                    <li>contact</li>
                </NavLink>
                <NavLink to="/knowledges" exact className="hover"
                activeClassName="nav-active">
                    <li>CV</li>
                </NavLink>
            </ul>   
        </div>
    );
} 

export default Navbar
